@import '../base.scss';

.c-page{
  &__header{
    background-color: $s-color--background-tertiary;
    &-inner{
      @include t-gutters;
      padding-top: t-to-rem(130);
      padding-bottom: t-to-rem(40);
    }

    &-heading{
      color: $s-color--font-6;
      text-align: center;
    }

  }
  &__inner{
    padding-top: 8%;
    padding-bottom: 8%;
    @include t-gutters;


    h1,
    h2,
    h3,
    h4,
    h5 {
      @include t-font--heading-5;
      margin-bottom: 2.0625rem;
      padding-top: 2.25rem;
    }

    ul,
    ol {
      padding-left: 2rem;
      list-style-type: disc;
      margin-bottom: 2rem;
    }

    li {
      @include t-font--type-base;
      color: $s-color--font-8;
    }
 
    p {
      margin-bottom: 2rem;
    }

    td {
      padding: .5rem;
    }

    .scrollbar {
      overflow-x: scroll;
      td {
        vertical-align: top;
      }
      table {
        min-width: 525px;
      }
    }
    /*    SCROLLBAR LIKE MAC OS    */
    .scrollbar::-webkit-scrollbar {
      background-color:#fff;
      width:16px
    }

    /* background of the scrollbar except button or resizer */
    .scrollbar::-webkit-scrollbar-track {
      background-color:#fff
    }
    .scrollbar::-webkit-scrollbar-track:hover {
      background-color:#f4f4f4
    }

    /* scrollbar itself */
    .scrollbar::-webkit-scrollbar-thumb {
      background-color:#babac0;
      border-radius:16px;
      border:5px solid #fff
    }
    .scrollbar::-webkit-scrollbar-thumb:hover {
      background-color:#a0a0a5;
      border:4px solid #f4f4f4
    }

    /* set button(top and bottom of the scrollbar) */
    .scrollbar::-webkit-scrollbar-button {display:none}
    /*    SCROLLBAR LIKE MAC OS    */
  }
  @include t-media-query($s-medium-up) {
    &__header{
      &-inner{
        padding-top: 13%;
        padding-bottom: 8%;

      }
    }

    &__inner{
      max-width: 65rem;
      margin: auto;
      td{
        padding: 1rem;
      }
    }
  }
}